<template>
  <div>
    <div v-if="getDataFinish">
      <el-header>
        <p style="margin:10px;font-size:14px;">
          <i class="el-icon-back" @click="goToSongList" style="position:absolute;left:20px;"></i>
          {{ songInfo[0].name }}
        </p>
      </el-header>

      <div>
        <img :src="songInfo[0].al.picUrl" />
        <div class="musicWords"></div>
        <div>
         
          <p style="width:100%;">
            <i v-if="loading" class="el-icon-loading"></i>
            <el-progress v-else :text-inside="true" :stroke-width="26" :percentage="percent"></el-progress>
          </p>
            <p>
            <span class="el-icon-caret-left icon"></span>

            <span @click="stopMusic" v-if="pauseMusic" class="el-icon-video-pause icon"></span>
            <span @click="playPause" v-else class="el-icon-video-play icon"></span>
            <span class="el-icon-caret-right icon"></span>
          <span @click="goComment" class="el-icon-chat-line-round icon"></span>
          </p>
        
         
          
          <el-divider><i class="el-icon-mobile-phone"></i></el-divider>
        

          <div style="color:#9E9E9E">
            建设中。。。
            <p>1:进度条问题：播放暂停改变进度条</p>
            <p>2:播放列表组件</p>
          </div>

        </div>
        <p v-if="getMusicUrlFinish">
          <audio ref="audio" id="music" controls="controls" autoplay :src="songUrl[0].url"></audio>
        </p>
      </div>
    </div>
    <div v-else>
      <i class="el-icon-loading"></i>
      <p>加载中</p>
    </div>
  </div>
</template>
<script>
import { constants } from "crypto";
export default {
  components: {},
  data() {
    return {
      //数据是否加载完成
      getDataFinish: false,
      songId: "",
      //歌曲信息
      songInfo: [],

      //歌曲资源url
      songUrl: [],
      getMusicUrlFinish: false,
      pauseMusic: true,

      //音乐进度条
      percent: 0,
      //音乐加载中
      loading: true,
    
    };
  },
  created() {
    //歌曲详情
    this.songId = this.$store.state.songId;
    this.$axios
      .get("http://120.27.243.36:3000/song/detail?ids=" + this.songId)
      .then(res => {
        this.songInfo = this.songInfo.concat(res.data.songs);
        this.getDataFinish = true;
      })
      .catch(err => {
        console.log(err);
      });
    //歌曲资源
    this.$axios
      .get("http://120.27.243.36:3000/song/url?id=" + this.songId)
      .then(res => {
        this.songUrl = this.songUrl.concat(res.data.data);
        this.getMusicUrlFinish = true;
      })
      .catch(err => {
        console.log(err);
      });
  },
  mounted() {
    setTimeout(() => {
      this.getAudio();
    }, 2000);
  },
  methods: {
    goToSongList() {
      this.$router.push("songList");
    },
    //播放音乐
    playPause() {
      const audio = document.getElementById("music");

      audio.play();
      this.pauseMusic = true;
    },
    //暂停播放
    stopMusic() {
      const audio = document.getElementById("music");
      audio.pause();
      this.pauseMusic = false;
      console.log(this.timer());
    },
    
    getAudio() {
      var _this = this;
      var aduioIsOk = setInterval(() => {
        const audio = document.getElementById("music");
        const allTime = audio.duration;
        const state = audio.readyState;
        const one = (allTime / 100) * 1000;
      
        if (state == 4) {
          _this.loading = false;
          clearInterval(aduioIsOk);   
          var timeControl = setInterval(() => {
            _this.percent += 1;
            if (_this.percent == 100) {
              clearInterval(timeControl);
              timeControl = null;
            }
          }, one);
          aduioIsOk = null;
        }
      }, 1000);
    },

    goComment(){
      this.$router.push('comment')
    }
  }
};
</script>
<style scoped>
p {
  width: 100%;
  height: 40px;
  line-height: 40px;
  margin: 0;
}
.el-icon-back:before {
  content: "\E6EA";
}
.el-icon-caret-left:before {
  content: "\E792";
}
.el-icon-video-pause:before {
  content: "\E7C1";
}
.el-icon-caret-right:before {
  content: "\E791";
}

.icon {
  font-size: 30px;
  margin: 0;
  line-height: 40px;
  margin: 0 30px;
}
img {
  width: 200px;
  height: 200px;
  border-radius: 100%;
  animation: run 10s linear 0s infinite;
}
@keyframes run {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
  }
}
.musicWords {
  width: 100%;
  height: 200px;
}
#music {
  width: 0;
  height: 0;
}
</style>